<template>
  <div>
    <div class="alert alert-danger text-center" v-if="error">
      {{ error }}
    </div>

    <div class="row justify-content-center">
      <div class="form-group">
        <label for="year">Year</label>
        <b-form-select id="year" :options="years.options" @input="onSelect"></b-form-select>
      </div>
    </div>

    <div class="row my-5 justify-content-center">
      <table class="table table-striped text-center" v-if="ready">
        <thead>
          <tr>
            <th scope="col">дата</th>
            <th scope="col">номер</th>
            <th scope="col">вид транзакция</th>
            <th scope="col">продадена валута</th>
            <th scope="col">количество продадена валута</th>
            <th scope="col">курс в долари</th>
            <th scope="col">курс лев/долар</th>
            <th scope="col">продажна цена в лева</th>
            <th scope="col">цена на придобиване в лева</th>
            <th scope="col">средна цена на придобиване в лева</th>
            <th scope="col">печалба/загуба</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="tx in transactions">
            <td>{{ tx.date }}</td>
            <td>{{ tx.transactionId }}</td>
            <td>{{ tx.type }}</td>

            <td>{{ tx.soldCcy }}</td>
            <td>{{ tx.soldCcyAmount }}</td>

            <td>{{ tx.soldCcyUSD }}</td>
            <td>{{ tx.usdToBGN }}</td>

            <td>{{ tx.salesProceedsBGN }}</td>
            <td>{{ tx.costBasisBGN }}</td>

            <td>{{ tx.avgPrice }}</td>
            <td>{{ tx.profitLoss }}</td>
          </tr>
        </tbody>
      </table>
    </div>

<!--    <div v-if="!total">There are no any transactions.</div>-->

    <loading :loading="loading"></loading>

  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    created() {
      this.loadTransaction(this.portfolioId)
    },
    props: ['portfolioId'],
    data() {
      return {
        loading: false,
        error: null,
        years: {
          // selected: 1,
          options: [],
        },
        pId: null,
        ready: false,
        transactions: null,
        currencies: null,
        total: null,
      }
    },
    methods: {
      loadTransaction(portfolioId) {
        this.pId = portfolioId
        let yearsId = 0
        axios.get(`${process.env.API_V1_URL}/portfolio/${portfolioId}/tx/years`)
          .then(res => {
            this.years.options = [{value: '', text: ''}].concat(res.data.map((date, i) => {
              return {value: ++yearsId, text: date}
            }))
          })
      },
      onSelect(v) {
        this.ready = false
        this.error = null
        this.loading = true

        return axios.get(`${process.env.API_V1_URL}/portfolio/${this.pId}/tx${this.years.options[v].text ? '/year/' + this.years.options[v].text : ''}`)
          .then(res => {
            this.transactions = res.data.transactions
            this.currencies = res.data.currencies
            this.total = res.count
          })
          .catch(errMsg => {
            this.error = errMsg
          })
          .finally(() => {
            this.loading = false
            this.ready = true
          })
      },
    },
    watch: {
      portfolioId(newId) {
        if (newId) {
          this.loadTransaction(newId)
        }
      },
    },
  }
</script>



